@font-face {
  src: url("font/GalanoGrotesqueAltRegular.0e190648.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesqueAltSemiBold.710dbe2b.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Bold.2be9a942.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Black.d7ff6920.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

:root {
  --font-family-primary: "galanogrotesquealt", sans-serif;
  --font-family-secondary: "galanogrotesque", sans-serif;
  --content-width: 1110px;
  --content-wide: 1742px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --container-wide: calc(var(--content-wide)  + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-dark-400: #1a1b24;
  --color-red-400: #e20403;
  --color-green-400: #378352;
  --color-green-300: #72c9cc;
  --color-gray-50: #fafafa;
  --color-gray-100: #b5b5b5;
  --color-gray-150: #919199;
  --color-blue-400: #0596e8;
  --color-gradient-main: linear-gradient(180deg, #80d0c8 0%, #0596e8 100%);
  --color-gradient-stock: linear-gradient(180deg, #7f99c6 0%, #00c3d0 100%);
  --color-gradient-crypto: linear-gradient(180deg, #d986c7 0%, #3c192c 100%);
  --color-gradient-side: linear-gradient(90deg, #aaa 0%, #fff0 100%);
  --color-gradient-border: linear-gradient(90deg, #fff0 0%, #fff 50.07%, #fff0 100%);
  --color-gradient-tab: linear-gradient(270deg, #fff0 0%, #aaa 52.31%, #fff0 100%);
  --color-gradient-forex: linear-gradient(180deg, #0d234a 0%, #6dc7cd 100%);
  --color-tab-active: linear-gradient(90deg, #0c99e7 0%, #fafafa00 55.49%);
  --color-gradient-article: linear-gradient(270deg, #0000 0%, #000 51.56%, #0000 100%);
  --gradient-background: linear-gradient(0deg, #f5f7fa4d -49.83%, #c3cfe24d 100%);
  --gradient-wellcome: linear-gradient(90deg, #000 0%, #68a0f8 100%);
  --gradient-wellcome-shadow: linear-gradient(180deg, #0b1628 10%, #03256400 100%);
  --shadow-ligth: 0 0 6px #00000005, 0px 2px 4px #00000014;
  --radius-btn: 100px;
  --radius-main: 6px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

.page {
  font-family: var(--font-family-primary);
  letter-spacing: .032em;
  color: var(--color-dark-400);
  background: var(--gradient-background);
  scroll-padding-top: var(--header-height, 100px);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.container--wide {
  max-width: var(--container-wide);
}

.main {
  min-height: calc(100vh - var(--header-height) * 2 + 47px);
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.section {
  padding: 5.125rem 0 5.75rem;
}

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.dis-scroll {
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.line {
  position: relative;
}

.line:before {
  content: "";
  z-index: 1;
  width: 100%;
  height: 1px;
  background: var(--color-gradient-tab);
  opacity: .5;
  margin: 0 auto;
  position: absolute;
  bottom: -28px;
  left: 0;
  right: 0;
}

.partnership-trading {
  padding: 1.0625rem 1.4375rem 1.0625rem 2.0625rem;
}

.partnership-trading .title {
  max-width: 360px;
}

.shadow-top {
  position: relative;
}

.shadow-top:before {
  content: "";
  z-index: 1;
  width: 100%;
  height: 75%;
  background: linear-gradient(#0b1628 0%, #03256400 100%);
  position: absolute;
  top: 0;
}

.shadow-bottom {
  position: relative;
}

.shadow-bottom:after {
  content: "";
  z-index: -1;
  width: 100%;
  height: 75%;
  background: linear-gradient(#0b1628 0%, #03256400 100%);
  position: absolute;
  bottom: 0;
  transform: matrix(1, 0, 0, -1, 0, 0);
}

.checkbox {
  letter-spacing: -.03em;
  position: relative;
}

.checkbox__field {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}

.checkbox__content {
  cursor: pointer;
  padding-left: 25px;
}

.checkbox__content:before {
  content: "";
  border-radius: calc(var(--radius-main) / 3);
  width: 15px;
  height: 15px;
  box-sizing: border-box;
  border: 1px solid #1a1a1a1a;
  position: absolute;
  top: 50%;
  left: 3px;
  overflow: hidden;
  transform: translateY(-50%);
}

.checkbox__content:after {
  content: "";
  width: 15px;
  height: 15px;
  box-sizing: border-box;
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='fi_32282' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='405.272px' height='405.272px' viewBox='0 0 405.272 405.272' style='enable-background:new 0 0 405.272 405.272;' xml:space='preserve'%3E%3Cg%3E%3Cpath d='M393.401,124.425L179.603,338.208c-15.832,15.835-41.514,15.835-57.361,0L11.878,227.836 c-15.838-15.835-15.838-41.52,0-57.358c15.841-15.841,41.521-15.841,57.355-0.006l81.698,81.699L336.037,67.064 c15.841-15.841,41.523-15.829,57.358,0C409.23,82.902,409.23,108.578,393.401,124.425z'%3E%3C/path%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 10px 10px;
  transition: opacity .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 3px;
  transform: translateY(-50%);
}

h1 {
  text-align: center;
}

.title {
  letter-spacing: normal;
  font-size: 65px;
  line-height: 1.2;
  font-family: var(--font-family-secondary);
  font-weight: 900;
}

.title span {
  color: #0000;
  background: var(--color-gradient-main);
  -webkit-background-clip: text;
  background-clip: text;
  display: inline-block;
}

.title--secondary {
  letter-spacing: normal;
  letter-spacing: .01em;
  color: var(--color-dark-400);
  margin-bottom: 1.6875rem;
  font-size: 45px;
  line-height: 1.2;
}

.title--main {
  letter-spacing: normal;
  color: var(--color-dark-400);
  font-size: 20px;
  font-weight: 700;
  line-height: 1.6;
}

.title--middle {
  letter-spacing: normal;
  font-size: 24px;
  line-height: 1.16667;
}

.title--small {
  letter-spacing: normal;
  color: var(--color-dark-400);
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
}

.hero {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero:after {
  content: "";
  width: 100%;
  height: 100%;
  max-width: 700px;
  max-height: 710px;
  background-image: url("images/home/women-face.3f8c7d59.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0;
  left: 0;
}

.hero__content {
  --translateY: -26px;
  z-index: 2;
  height: calc(100% - var(--translateY));
  transform: translateY(var(--translateY));
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.hero__title {
  letter-spacing: normal;
  text-transform: capitalize;
  color: var(--color-white);
  margin-bottom: 15px;
  font-size: 65px;
  font-weight: 700;
  line-height: 1.2;
}

.hero__title span {
  color: #0000;
  background: var(--color-gradient-main);
  -webkit-background-clip: text;
  background-clip: text;
  display: block;
}

.hero__text {
  letter-spacing: normal;
  width: 100%;
  max-width: 435px;
  text-align: center;
  color: var(--color-white);
  margin-bottom: 2.0625rem;
  font-size: 18px;
  line-height: 1.61111;
}

.hero__btn {
  max-width: 255px;
}

.instruments {
  background-color: var(--color-white);
}

.instruments__list {
  z-index: 1;
  color: var(--color-white);
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
  display: grid;
  position: relative;
}

.instruments__item {
  border-radius: var(--radius-main);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1.5rem 1.625rem 22px;
  position: relative;
  overflow: hidden;
}

.instruments__item:before {
  content: "";
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.instruments__item--forex {
  padding: 1.875rem 2.3125rem 22px;
}

.instruments__item--forex:before {
  background: var(--color-gradient-forex);
}

.instruments__item--forex .instruments__icon {
  margin-bottom: 5rem;
}

.instruments__item--stocks:before {
  background: var(--color-gradient-stock);
}

.instruments__item--stocks .instruments__icon {
  margin-bottom: 5.3125rem;
}

.instruments__item--crypto {
  padding: 1.25rem 1.8125rem;
}

.instruments__item--crypto:before {
  background: var(--color-gradient-crypto);
}

.instruments__item--crypto .instruments__icon {
  margin-bottom: 5.625rem;
}

.instruments__icon {
  height: 72px;
  max-width: 72px;
}

.instruments__item-title {
  text-align: left;
  text-transform: capitalize;
  color: var(--color-white);
  margin-bottom: 1.875rem;
  position: relative;
}

.instruments__item-title:before {
  content: "";
  width: 100%;
  height: 1px;
  max-width: 125px;
  background: var(--color-gradient-side);
  position: absolute;
  bottom: -15px;
  left: 0;
}

.instruments__item-title:after {
  letter-spacing: normal;
  content: attr(data-text);
  z-index: 0;
  -webkit-text-stroke: 1px var(--color-white);
  color: #0000;
  opacity: .3;
  font-size: 35px;
  font-weight: 900;
  line-height: .914286;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(9px, -16px);
}

.instruments__text {
  letter-spacing: normal;
  font-size: 16px;
  line-height: 1.625;
}

.trade-with {
  background: var(--gradient-background);
  padding-bottom: 6.25rem;
}

.trade-with__list {
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  display: grid;
}

.trade-with__item {
  border-radius: var(--radius-main);
  box-shadow: var(--shadow-ligth);
  background-color: var(--color-white);
  padding: 2rem clamp(20px, 1.875rem, 30px);
  position: relative;
  overflow: hidden;
}

.trade-with__item:before {
  content: "";
  width: 100%;
  height: 88px;
  max-width: 88px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}

.trade-with__item > * {
  text-align: left;
}

.trade-with__item:nth-child(2n+1):before {
  right: clamp(15px, 1.4375rem, 23px);
}

.trade-with__item:nth-child(2n):before {
  left: clamp(20px, 2.25rem, 36px);
}

.trade-with__item:nth-child(2n) > * {
  text-align: right;
  margin-left: auto;
}

.trade-with__item:nth-child(n+3) {
  padding: 1.625rem 1.875rem 2.5rem;
}

.trade-with__item:nth-child(n+3):before {
  top: clamp(20px, 2.125rem, 34px);
}

.trade-with__item--profit:before {
  background-image: url("icons/make-profit.70dca3dd.svg");
}

.trade-with__item--awward:before {
  background-image: url("icons/awward.d8603cba.svg");
}

.trade-with__item--shield:before {
  background-image: url("icons/shield.e7aa97b4.svg");
}

.trade-with__item--team:before {
  background-image: url("icons/team.3ceb731f.svg");
}

.trade-with__item-title {
  margin-bottom: 10px;
}

.trade-with__text {
  width: 100%;
  max-width: 350px;
}

.start {
  color: var(--color-white);
  background-image: url("images/home/start-bg.e7a50846.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--color-dark-400);
  justify-content: flex-end;
  display: flex;
}

.start__content {
  width: 100%;
  max-width: 395px;
  margin-right: 10rem;
  padding-top: 5rem;
}

.start__title {
  text-align: left;
  color: inherit;
}

.start__title span {
  display: block;
}

.start__item-title {
  min-height: 54px;
  text-align: left;
  color: inherit;
  align-items: center;
  display: flex;
}

.start__steps {
  padding-left: 4rem;
}

.start__step {
  counter-increment: steps;
  position: relative;
}

.start__step:before {
  letter-spacing: normal;
  content: counter(steps);
  width: 42px;
  height: 42px;
  background: var(--color-gradient-main);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 900;
  line-height: 1.2;
  display: flex;
  position: absolute;
  top: 7px;
  left: -64px;
}

.start__step:not(:last-child) {
  margin-bottom: 2.5625rem;
}

.start__step:not(:last-child):after {
  content: "";
  width: 1px;
  height: 100%;
  max-height: 80px;
  background-color: var(--color-green-300);
  position: absolute;
  bottom: -40px;
  left: -44px;
}

.start__picture {
  max-width: 960px;
}

.news {
  background: var(--gradient-background);
  padding-bottom: 7.9375rem;
}

.news__list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.get-started {
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--color-white);
  padding-top: 4.5rem;
  padding-bottom: 4.875rem;
  position: relative;
}

.get-started__inner {
  border-radius: var(--radius-main);
  width: 100%;
  background-image: url("images/home/started-bg.c39d0baf.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 4.5625rem 20px;
  overflow: hidden;
}

.get-started__picture {
  position: absolute;
  top: 0;
  transform: translate(36%, 21px);
}

.get-started__title {
  margin-bottom: 8px;
}

.get-started__content {
  width: 100%;
  max-width: 380px;
  margin: 0 auto;
  transform: translateX(5.625rem);
}

.get-started__text {
  margin-bottom: 1.3125rem;
}

.get-started__link:not(:last-child) {
  margin-right: 7px;
}

.get-started__btns {
  display: flex;
}

.wellcome {
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10rem 0 4.6875rem;
  position: relative;
}

.wellcome:before {
  content: "";
  width: 100%;
  height: 100%;
  background: var(--gradient-wellcome-shadow);
  position: absolute;
  top: 0;
}

.wellcome:after {
  content: "";
  z-index: -1;
  width: 100%;
  height: 100%;
  background: var(--gradient-wellcome);
  position: absolute;
  top: 0;
}

.wellcome__title {
  z-index: 1;
  text-transform: capitalize;
  color: var(--color-white);
  position: relative;
}

.wellcome__title span {
  display: block;
}

.single {
  background: var(--gradient-background);
  padding: 5.4375rem 0 6.25rem;
  position: relative;
}

.single__inner {
  justify-content: space-between;
  display: flex;
}

.single__navigation {
  top: var(--header-height);
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 255px;
  position: sticky;
}

.single__content {
  letter-spacing: normal;
  width: 100%;
  max-width: 825px;
  font-size: 16px;
  line-height: 1.625;
}

.single__content > * {
  letter-spacing: .03em;
}

.single__content > :not(:last-child, .content__list, .classification) {
  margin-bottom: 1.625rem;
}

.single__content blockquote {
  border: 1px solid var(--color-blue-400);
  border-radius: 0 var(--radius-main) var(--radius-main) 0;
  background-color: var(--color-white);
  border-left-width: 5px;
  justify-content: space-between;
  padding: 1.5rem 2.375rem 1.1875rem;
  font-weight: 700;
  display: flex;
  overflow: hidden;
}

.single__content--strategy.single__content > p {
  margin-bottom: 15px;
}

.single__content--strategy.single__content h2 {
  letter-spacing: 0;
  margin-bottom: 18px;
}

.single__content--out.single__content h2 {
  margin-bottom: 8px;
}

.single__list {
  grid-template-columns: repeat(auto-fill, minmax(255px, 1fr));
  gap: 1.875rem;
  margin-bottom: 2.1875rem;
  padding-top: 3px;
  display: grid;
}

.single img {
  border-radius: var(--radius-main);
  box-shadow: var(--shadow-ligth);
  overflow: hidden;
}

.single--bottom {
  padding-bottom: 4.875rem;
}

.strengths {
  margin-bottom: 1.4375rem;
}

.strengths__title {
  letter-spacing: normal;
  text-align: left;
  margin-bottom: 15px;
  font-size: 24px;
  line-height: 1.16667;
}

.strengths__list {
  grid-template-columns: repeat(2, 1fr);
  gap: 1.875rem;
  display: grid;
}

.strengths__item {
  counter-increment: strengths;
  border-radius: var(--radius-main);
  background-color: var(--color-white);
  padding: 17px;
  overflow: hidden;
}

.strengths__item:before {
  color: #0000;
  background: var(--color-gradient-main);
  letter-spacing: normal;
  content: counter(strengths, decimal-leading-zero);
  font-size: 85px;
  line-height: 1.17647;
  font-family: var(--font-family-secondary);
  letter-spacing: .5px;
  background-color: #0000;
  background-image: url("images/list-counter/manager.54f88e21.png"), linear-gradient(#80d0c8 0%, #0596e8 100%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 900;
  position: absolute;
}

.strengths__item--manager:before {
  background-image: url("images/list-counter/manager.54f88e21.png"), linear-gradient(#80d0c8 0%, #0596e8 100%);
}

.strengths__item--traders:before {
  background-image: url("images/list-counter/traders.80b698b5.png"), linear-gradient(#80d0c8 0%, #0596e8 100%);
}

.strengths__item--support:before {
  background-image: url("images/list-counter/support.7f76526f.png"), linear-gradient(#80d0c8 0%, #0596e8 100%);
}

.strengths__item--respect:before {
  background-image: url("images/list-counter/respect.e0ee2dfa.png"), linear-gradient(#80d0c8 0%, #0596e8 100%);
}

.strengths__item-title {
  text-align: left;
  margin-bottom: 25px;
  position: relative;
}

.strengths__item-title:before {
  content: "";
  z-index: 1;
  width: 100%;
  height: 1px;
  background: var(--color-gradient-tab);
  opacity: .5;
  background: var(--color-gradient-side);
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  left: 0;
